import "./import-jquery.js";
import "jquery-ui-bundle"; // you also need this
import "jquery-ui-bundle/jquery-ui.css";
import draw2d from "draw2d_aprover";
import LeftPortKnowledgeMarkAtk from "./LeftPortKnowledgeMarkAtk.js";
import RightPortKnowledgeMarkAtk from "./RightPortKnowledgeMarkAtk.js";

export default draw2d.shape.basic.Rectangle.extend({
    NAME: 'KnowledgeMarkAtk',
    init: function (attr, setter, getter) {
        this._super($.extend({ bgColor: '#db4c4c', color: '#cc000', height: "20", width: "10" }),
            $.extend({
                messageNumb: this.setMessageNumb,
            }, setter),
            getter, attr);


    },

    setMessageNumb: function (n) {
        this.messageNumb = n;
        console.log(n);
        if (n % 2 == 0) {
            var output = new LeftPortKnowledgeMarkAtk();
            this.createPort("output", output);
            var input = new RightPortKnowledgeMarkAtk();
            this.createPort("input", input);
        } else {
            var output = new RightPortKnowledgeMarkAtk();
            this.createPort("output", output);
            var input = new LeftPortKnowledgeMarkAtk();
            this.createPort("input", input);
        }
        return this;

    },
    /**
     * @method
     * Called if the user drop this element onto the dropTarget. 
     * 
     * In this Example we create a "smart insert" of an existing connection.
     * COOL and fast network editing.
     * 
     * @param {draw2d.Figure} dropTarget The drop target.
     * @param {Number} x the x coordinate of the drop
     * @param {Number} y the y coordinate of the drop
     * @param {Boolean} shiftKey true if the shift key has been pressed during this event
     * @param {Boolean} ctrlKey true if the ctrl key has been pressed during the event
     * @private
     **/
    onDrop: function (dropTarget, x, y, shiftKey, ctrlKey) {
        //console.log("onDrop");
        //console.log("x: " + x + " y: " + y);
        // Activate a "smart insert" If the user drop this figure on connection
        //
        if (dropTarget instanceof draw2d.Connection) {
            let oldSource = dropTarget.getSource();
            let oldTarget = dropTarget.getTarget();

            let insertionSource = this.getOutputPort(0)
            let insertionTarget = this.getInputPort(0)

            // ensure that oldSource ---> insertionTarget.... insertionSource ------>oldTarget
            //
            //console.log(oldSource);
            if (oldSource instanceof draw2d.InputPort) {
                oldSource = dropTarget.getTarget();
                oldTarget = dropTarget.getSource();
            }

            let stack = this.getCanvas().getCommandStack();

            let cmd = new draw2d.command.CommandReconnect(dropTarget);
            cmd.setNewPorts(oldSource, insertionTarget);
            stack.execute(cmd);

            let additionalConnection = window.createConnection();
            //console.log(insertionSource);
            //console.log(oldTarget);
            cmd = new draw2d.command.CommandConnect(insertionSource, oldTarget);
            cmd.setConnection(additionalConnection);
            stack.execute(cmd);
        } else {
            console.log("not connection");
        }
    },

    onRemove: function () {
        //let oldSource = reconnectTarget.getSource();
        //let oldTarget = reconnectTarget.getTarget();
        
        let insertionSource = this.getOutputPort(0);
        let insertionTarget = this.getInputPort(0);
        let connection = insertionSource.getConnections().get(0);
        let newSource = insertionTarget.getConnections().get(0).getSource();
        //let reconnectTarget = insertionTarget.getConnections().get(0).getSource().getParent();
        
       /*  if (this.messageNumb % 2 == 0) {
            insertionSource = this.getInputPort(0);
            insertionTarget = this.getOutputPort(0);
            connection = insertionSource.getConnections().get(0);
            newSource = insertionSource.getConnections().get(0).getTarget();
            reconnectTarget = insertionSource.getConnections().get(0).getTarget().getParent();
            window.canvas.remove(insertionSource.getConnections().get(0));
            window.canvas.remove(this);
            connection.setTarget(newSource);
        }else{ */
            window.canvas.remove(insertionTarget.getConnections().get(0));
            window.canvas.remove(this);
            connection.setSource(newSource);
        //}
        

        //console.log(reconnectTarget);
        
        // ensure that oldSource ---> insertionTarget.... insertionSource ------>oldTarget
        //
        //console.log(oldSource);
        /*  if (oldSource instanceof draw2d.InputPort) {
             oldSource = dropTarget.getTarget();
             oldTarget = dropTarget.getSource();
         } */

        /* 
                let stack = this.getCanvas().getCommandStack();
                let cmd = new draw2d.command.CommandReconnect(reconnectTarget);
                cmd.setNewPorts(insertionSource,oldSource);
                stack.execute(cmd); */

    }

});