let checkedServer = document.querySelector("#checkbox1");
checkedServer.addEventListener('change', function () {
    if (this.checked) {
        sidebarBtnServ.style.visibility = "visible";
        window.server = new Principal("Server", window.canvas);
    } else {
        if (window.server.hasIncomingMesssages() == false) {
            sidebarBtnServ.style.visibility = "hidden";
            window.server.setVisibility(false);
        }

    }
});

let checkedAtk = document.querySelector("#checkbox2");
checkedAtk.addEventListener('change', function () {
    if (this.checked) {

        window.alice.translate('left');
        window.attacker = new Principal("Attacker", window.canvas);
        var messages = window.canvas.getLines().clone();
        var num_mess = 1
        for (let i = 0; i < messages.getSize(); i++) {
            var element = messages.get(i);
            if (element.NAME != 'TimeLinePrincipal') {
                console.log("MAIN");
                console.log(num_mess);
                let prov = new KnowledgeMarkAtk($.extend({ messageNumb: num_mess }));
                window.canvas.add(prov, window.attacker.getTimeLineUpX() - 5, element.getStartY() - 10);
                prov.onDrop(element, prov.getAbsoluteX(), prov.getAbsoluteY(), false, false);
                num_mess = num_mess + 1;
            }
            //console.log(messages.length);
        }

    } else {
        window.attacker.setVisibility(false);
        var marks_atk = window.canvas.getFigures().clone();

        for (let i = 0; i < marks_atk.getSize(); i++) {
            var element = marks_atk.get(i);
            if (element.NAME == 'KnowledgeMarkAtk') {
                element.onRemove();
                
            }
            
        }
        window.alice.translate('right');
    }
});

