import "./import-jquery.js";
import "jquery-ui-bundle"; // you also need this
import "jquery-ui-bundle/jquery-ui.css";
import draw2d from "draw2d_aprover";
import PrincipalLabel from "./PrincipalLabel.js";
import OutputPrincipalPortLocator from "./OutputPrincipalPortLocator.js";
import EndPrincipal from "./EndPrincipal.js";
import InputPrincipalPortLocator from "./InputPrincipalPortLocator.js";
import WorkArea from "./WorkArea.js";
import TimeLinePrincipal from "./TimeLinePrincipal.js";
import SelectionMenuPolicy from "./SelectionMenuPolicy.js";


export default Class.extend(
    {
        NAME: "Principal",

        init: function (name, canvas) {

            this.knowledgemarks = [];
            // register this class as event listener for the canvas
            // CommandStack. This is required to update the state of 
            // the Undo/Redo Buttons.
            //
            //canvas.getCommandStack().addEventListener(this);

            // Register a Selection listener for the state hnadling
            // of the Delete Button
            //
            this.name = name;
            canvas.on("select", $.proxy(this.onSelectionChanged, this));
            this.principalTop = new PrincipalLabel();
            this.principalTop.setActor(name);
            this.principalTop.createPort("hybrid", new OutputPrincipalPortLocator());
            this.principalTop.setSelectable(false);
            this.principalTop.setDraggable(false);
            //this.principalTop.installEditPolicy(new draw2d.policy.figure.AntSelectionFeedbackPolicy());
            this.principalEnd = new EndPrincipal();
            this.principalEnd.createPort("hybrid", new InputPrincipalPortLocator());
            //this.principalEnd.installEditPolicy(new draw2d.policy.figure.AntSelectionFeedbackPolicy());

            this.jail = new WorkArea($.extend({ height: canvas.getHeight(), width: (canvas.getWidth() / 4) }));
            //this.jail.installEditPolicy(new draw2d.policy.figure.AntSelectionFeedbackPolicy());
            this.jail.setBackgroundColor(null);
            //add calculation
            if (name == "Alice") {
                canvas.add(this.jail, canvas.getWidth() / 4, 0);
                canvas.add(this.principalTop, 3 * (canvas.getWidth() / 8) - 60, 20);
                canvas.add(this.principalEnd, 3 * (canvas.getWidth() / 8) - 75, canvas.getHeight() - 30);

            } else if (name == "Bob") {
                canvas.add(this.jail, canvas.getWidth() / 2, 0);
                canvas.add(this.principalTop, 5 * (canvas.getWidth() / 8) - 60, 20);
                canvas.add(this.principalEnd, 5 * (canvas.getWidth() / 8) - 75, canvas.getHeight() - 30);
                this.principalTop.setActorColor("#38761d");

            } else if (name == "Server") {
                canvas.add(this.jail, 3 * (canvas.getWidth()) / 4, 0);
                canvas.add(this.principalTop, 7 * (canvas.getWidth() / 8) - 60, 20);
                canvas.add(this.principalEnd, 7 * (canvas.getWidth() / 8) - 75, canvas.getHeight() - 30);
                this.principalTop.setActorColor("#a64d79");
            } else {
                //canvas.add(this.jail, canvas.getWidth() / 4, 0);
                canvas.add(this.principalTop, 3 * (canvas.getWidth() / 8) - 60, 20);
                canvas.add(this.principalEnd, 3 * (canvas.getWidth() / 8) - 75, canvas.getHeight() - 30);
                this.principalTop.setActorColor("#cc0000");
            }

            this.timeLine = new TimeLinePrincipal({
                source: this.principalTop.getPorts().first(),
                target: this.principalEnd.getPorts().first()
            });

            canvas.add(this.timeLine);
            //this.jail.toBack();
            //let bobTimeLen = this.timeLine.getLength();
            /* let timeLineUpX = this.timeLine.getStartX();
            let timeLineUpY = this.timeLine.getStartY();

            this.principalTopX = this.principalTop.getAbsoluteX();
            this.principalTopY = this.principalTop.getAbsoluteY();
            this.princiaplEndX = this.principalEnd.getAbsoluteX();
            this.princiaplEndY = this.principalEnd.getAbsoluteY();
            this.jailX = this.jail.getAbsoluteX();
            this.jailY = this.jail.getAbsoluteY(); */



            //this.jail.assignFigure(this.principalTop);
            //this.jail.assignFigure(this.principalEnd);

            //this.principalTop.installEditPolicy(new draw2d.policy.figure.RegionEditPolicy(this.principalTopX, this.principalTopY, this.principalTop.getWidth(), this.principalTop.getHeight()));
            //this.principalEnd.installEditPolicy(new draw2d.policy.figure.RegionEditPolicy(this.princiaplEndX, this.princiaplEndY, this.principalEnd.getWidth(), this.principalEnd.getHeight()));
            //this.jail.installEditPolicy(new draw2d.policy.figure.RegionEditPolicy(this.jailX, this.jailY, this.jail.getWidth()+1, this.jail.getHeight()+1));


        },

        getName: function () {
            return this.name;
        },



        getTopX: function () {

            return this.principalTop.getAbsoluteX();

        },

        getTopY: function () {

            return this.principalTop.getAbsoluteY();

        },
        getEndX: function () {

            return this.principalEnd.getAbsoluteX();

        },

        getEndY: function () {

            return this.principalEnd.getAbsoluteY();

        },

        getJailX: function () {

            return this.jail.getAbsoluteX();

        },

        getJailY: function () {

            return this.jail.getAbsoluteY();

        },

        getTimeLineUpX: function () {

            return this.timeLine.getStartX();

        },

        getTimeLineUpY: function () {

            return this.timeLine.getStartY();

        },

        hasIncomingMesssages: function () {

            return false;

        },

        setVisibility: function (hide) {
            if (hide == false) {


                var command1 = new draw2d.command.CommandDelete(this.principalTop);
                canvas.getCommandStack().execute(command1);
                var command2 = new draw2d.command.CommandDelete(this.principalEnd);
                canvas.getCommandStack().execute(command2);
                var command3 = new draw2d.command.CommandDelete(this.jail);
                canvas.getCommandStack().execute(command3);


            }

        },

        translate: function (side) {
            switch (side) {
                case 'left':
                    this.principalEnd.translate((-(canvas.getWidth() / 4)), 0);
                    this.principalTop.translate((-(canvas.getWidth() / 4)), 0);
                    this.jail.translate((-(canvas.getWidth() / 4)), 0);
                    //this.knowledgemarks.setSelectable(false);
                    for (const element of this.knowledgemarks) {
                        if (element.isSelected()) {
                            element.editPolicy.data[0].onUnselect();
                        }
                        element.uninstallEditPolicy(new draw2d.policy.figure.RegionEditPolicy(element.getAbsoluteX(), element.getAbsoluteY(), element.getWidth(), element.getHeight()));
                        element.translate((-(canvas.getWidth() / 4)), 0);
                        element.installEditPolicy(new draw2d.policy.figure.RegionEditPolicy(element.getAbsoluteX(), element.getAbsoluteY(), element.getWidth(), element.getHeight()));
                        if (element.isSelected()) {
                            element.editPolicy.data[0].onSelect(canvas, element, true);
                        }
                    }
                    break;
                case 'right':
                    this.principalEnd.translate(((canvas.getWidth() / 4)), 0);
                    this.principalTop.translate(((canvas.getWidth() / 4)), 0);
                    this.jail.translate(((canvas.getWidth() / 4)), 0);
                    
                    for (const element of this.knowledgemarks) {
                        element.uninstallEditPolicy(new draw2d.policy.figure.RegionEditPolicy(element.getAbsoluteX(), element.getAbsoluteY(), element.getWidth(), element.getHeight()));
                        element.translate(((canvas.getWidth() / 4)), 0);
                        element.installEditPolicy(new draw2d.policy.figure.RegionEditPolicy(element.getAbsoluteX(), element.getAbsoluteY(), element.getWidth(), element.getHeight()));
                    }
                    break;
                default:
                    console.log(`Sorry, the movement ${expr} is unkwown.`);
            }


        },

        addKnowledgeMark: function (kmark) {
            this.knowledgemarks.push(kmark);
        },

        lastKnowledgeMark: function () {
            return this.knowledgemarks[this.knowledgemarks.length - 1];
        },
        hideTimeLine: function () {
            this.timeLine.setVisible(false);
        }




    });
